@import url(https://fonts.googleapis.com/css?family=Cinzel+Decorative|Montserrat:500|Open+Sans);

#banner {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: 600px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  padding: 20px;
  align-items: flex-end;
  color: white;
  font-size: 1.3em;
}

.main-content {
  max-width: 1000px;
  margin: 0 auto;
}

.content-box {
  display: flex;
  justify-content: space-evenly;
  margin: 30px 0;
  text-align: center;
}
.content-box img {
  width: 40%;
  height: auto;
  display: block;
}

.layover {
  background-color: rgba(8, 8, 8, 0.75);
  text-align: center;
  padding: 10px;
  border-radius: 20px;
}

.headline {
  text-align: center;
  margin: 50px auto;
}

hr {
  max-width: 1000px;
  margin: 30px auto;
}

@media only screen and (max-width: 800px) {
  #banner {
    height: 75vw;
    font-size: 0.8em;
  }
}
@media only screen and (max-width: 500px) {
  #banner {
    height: 75vw;
    font-size: 0.5em;
  }
  .layover {
    background-color: rgba(8, 8, 8, 0.3);
  }
}




.floor {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.floor img {
  width: 50%;
  height: auto;
}
.floor li {
  line-height: 1.5;
  font-size: 15px;
}

#header {
  display: flex;
  justify-content: space-between;
}

#contact-link {
  background-color: rgb(58, 58, 250);
  border-radius: 10px;
  color: white !important;
}
#logo {
  margin: 2px;
  background-color: #dfe1e9;
  border-radius: 30px 10px;
  padding: 15px;
  color: #555;
}

.under-header p {
  text-align: center;
}

#header a:hover {
  color: #777;
}
#navbar {
  margin: 30px 30px 30px 0;
}

#navbar a {
  padding: 7px;
  color: #555;
}

#footer {
  background-color: lightblue;
  text-align: center;
  padding: 30px;
}

body {
  background-color: #ebedf3;
  color: #555;
  padding: 0;
}

h1,
h2 {
  font-family: 'Cinzel Decorative', cursive;
}

/* h3,
h4 {
  font-family: 'Montserrat', sans-serif;
} */

h3,
h4,
p,
li,
ul,
a,
div,
span {
  font-family: 'Open Sans', sans-serif;
}

a {
  text-decoration: none;
}

